.self-introduce {
  position: relative;
  background-image: url("../resources/background.jpg");
  background-size: cover;
  height: 100vh;
}

.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.profile video {
  position: fixed;
  z-index: -100;
}

.description {
  border: 3px solid white;
  margin: 30px 0px 30px 0px;
  padding: 0px 10px 0px 10px;
  text-align: center;
  font-family: noto_sans_krbold;
  line-height: 100px;
  font-weight: bold;
  font-size: 40px;
  backdrop-filter: blur(10px);
  color: white;
}

.profile-image {
  margin-top: 100px;
  width: 500px;
  height: 500px;
  box-shadow: 10px 10px 20px black;
}

.languages-list {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("../resources/background2.jpg");
  background-size: cover;
  height: 150vh;
}

.languages-list img {
  width: 250px;
  height: 250px;
}

.use-languages {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 50px;
  padding-bottom: 70px;
  border-bottom: 3px solid black;
}

.used-languages {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.use-languages h1 {
  font-size: 50px;
  text-align: center;
}

.used-languages h1 {
  font-size: 40px;
  text-align: center;
}

.use-languages-list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 30px;
}

.used-languages-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-top: 30px;
}

.use-languages-list div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(4px);
  padding: 10px;
}

.used-languages-list div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(4px);
  padding: 10px;
  margin-bottom: 30px;
}

.use-languages-list div span {
  font-family: noto_sans_krbold;
  font-weight: bold;
  font-size: 20px;
}

.used-languages-list div span {
  font-family: noto_sans_krbold;
  font-weight: bold;
  font-size: 20px;
}

.introduce-text-center {
  text-align: center;
}

.introduce-text-right {
  text-align: right;
}

.introduce-text-area {
  font-family: noto_sans_krblack;
  font-size: 50px;
  color: white;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-image: url("../resources/background3.jpg");
  background-size: cover;
  padding: 20px;
  height: 100vh;
}

@media screen and (min-width: 301px) and (max-width: 850px) {
  .self-introduce {
    background-position: center center;
  }

  .languages-list {
    background-position: center center;
    height: 100vh;
  }

  .profile-image {
    margin-top: 100px;
    width: 300px;
    height: 300px;
    box-shadow: 10px 10px 20px black;
  }

  .description {
    margin-top: 100px;
    line-height: 50px;
    font-weight: bold;
    font-size: 18px;
  }

  .use-languages h1 {
    font-size: 30px;
  }

  .used-languages h1 {
    font-size: 30px;
  }

  .languages-list img {
    width: 80px;
    height: 80px;
  }

  .introduce-text-area {
    font-size: 40px;
  }
}

@media screen and (max-width: 300px) {
  .self-introduce {
    background-position: center center;
    height: 130vh;
  }

  .languages-list {
    background-position: center center;
    height: 170vh;
  }

  .profile-image {
    margin-top: 100px;
    width: 300px;
    height: 300px;
    box-shadow: 10px 10px 20px black;
  }

  .description {
    margin-top: 100px;
    line-height: 50px;
    font-weight: bold;
    font-size: 18px;
  }

  .use-languages h1 {
    font-size: 30px;
  }

  .used-languages h1 {
    font-size: 30px;
  }

  .languages-list img {
    width: 80px;
    height: 80px;
  }

  .introduce-text-area {
    font-size: 20px;
  }
}

@media screen and (min-width: 301px) and (max-height: 800px) {
  .languages-list {
    height: 130vh;
  }
}

@media screen and (max-height: 600px) {
  .self-introduce {
    height: 150vh;
  }
  .languages-list {
    height: 200vh;
  }
}
