.contact-area {
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.contact-box {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 10px white;
  width: 500px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact-box h1 {
  color: grey;
  text-align: center;
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid grey;
}

.your-email-address,
.contact-title,
.contact-message {
  margin: 20px;
  width: 80%;
  height: 10%;
  text-align: center;
}

.contact-message {
  border-bottom: 1px solid grey;
}

.your-email-address input,
.contact-title input {
  width: 100%;
}

.contact-message textarea {
  resize: none;
  width: 100%;
  height: 100%;
}

.send-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 30px;
  padding-top: 20px;
  border-top: 1px solid grey;
}

.button {
  width: 100px;
  border-radius: 10px;
  background-color: #f8585b;
  border: none;
  color: #fff;
  padding: 15px 0;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  margin: 4px;
  cursor: pointer;
  outline: 0;
}

@media screen and (max-height: 800px) {
  .contact-area {
    height: 130vh;
  }
}
