.git-list-area {
  background-image: url("../resources/background4.jpg");
  background-size: cover;
  height: 110vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.git-lists {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  height: 100%;
}

.git-list-area h1 {
  color: white;
  font-family: noto_sans_krblack;
  font-weight: bold;
  font-size: 50px;
  margin-bottom: 50px;
  margin-top: 200px;
}

.git-list-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 300px;
  margin: 30px;
  background-color: white;
  border-radius: 20px;
  box-shadow: 0px 0px 30px 10px black;
  cursor: pointer;
}

.git-list-item-name {
  font-family: noto_sans_krbold;
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 20px;
}

.git-list-item-img {
  width: 150px;
  height: 150px;
}

.modal {
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.git-item-info-modal {
  width: 500px;
  height: 500px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 20px 10px black;
}

.close-button {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  border-bottom: 3px solid gray;
}
.close-button img {
  width: 48px;
  height: 48px;
  cursor: pointer;
}

.git-item-info-data {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 70%;
}

.data-field {
  font-family: noto_sans_krbold;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  width: 100%;
  padding: 20px;
  border-bottom: 1px solid black;
}

.data-field span {
  font-weight: normal;
  font-size: 20px;
}

.git-button {
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 280px) {
  .git-list-area {
    height: 400vh;
  }

  .git-item-info-modal {
    width: 200px;
    height: 450px;
  }

  .data-field {
    font-size: 15px;
  }
  .data-field span {
    font-size: 15px;
  }
}

